let burger = document.querySelector(".burger")
let body = document.querySelector("body")
let header = document.querySelector(".header")
let menuClose = document.querySelector(".menu__bg")
let menuLinks = document.querySelectorAll(".menu__link")


if (burger) {
    burger.onclick = function() {
        header.classList.toggle("_open")
        body.classList.toggle("fixed-body")
    }

    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(header);

        if ( ! withinBoundaries ) {
            closeMenu()
        }
    })
}

if(menuClose){
    menuClose.onclick = function () {
        closeMenu()
    }
}

function closeMenu(){
    header.classList.remove("_open")
    body.classList.remove("fixed-body")
}

menuLinks.forEach(link => {
    link.onclick = function () {
        closeMenu()
    }
})


window.onscroll = function (e) {
    setFixedHeader()
};


function setFixedHeader() {
    let headerHeight = getHeaderHeight()
    let st = window.scrollY;
    if(st > headerHeight * 1){
        header.classList.add("_transform")
    } else{
        header.classList.remove("_transform")
    }
    if(st > headerHeight * 1.1){
        header.classList.add("_fix")
    } else{
        header.classList.remove("_fix")
    }
}

function getHeaderHeight() {
    if(!header){
        return
    }
    return header.getBoundingClientRect().height
}
